import React from 'react';
import _ from 'lodash';

import {Spinner, Combobox, Input, Datepicker, Button, Checkbox, Tooltip, Icon} from '@salesforce/design-system-react';

import {comboboxFilterAndLimit} from '../lib/utils';

const SALESFORCE_FIELD = 'Salesforce Field';
const MAPPING = 'Option';
const DEFAULT_VALUE = 'Value';
const IS_OVERRIDE = 'Is Override?';


class GRAXRestoreMappingSFField extends React.Component {
    constructor(props) {
		super(props);

        this._isMounted = false;

		this.state = {
			inputValue: '',
            selection: [],
            isOpened: false
		};
    }

    componentDidMount() {
        this._isMounted = true;

        if(this.props.selection) {
            this.setState({
                selection: this.props.selection
            })
        }
    }

    componentWillUnmount() {
        this._isMounted = false;
    }
    
    filterOptionsHandler() {
        var options = this.props.options;

        if(this.props.filterOptionsHandler && this.state.isOpened) {
            options = this.props.filterOptionsHandler(options);
        }

        return comboboxFilterAndLimit({
            inputValue: this.state.inputValue,
            limit: 1000,
            options: options,
            selection: this.state.selection
        })
    }

    render() {
        return (
            <React.Fragment>
                <Combobox
                    classNameMenu={'scrollable-GRAXRestoreMappingSFField'}
                    events={{
                        onChange: (event, { value }) => {
                            if(this._isMounted) {
                                this.setState({
                                    inputValue: value
                                });
                            }

                            if (this.props.onChange) {
                                this.props.onChange(event, value);
                            }
                        },
                        onRequestRemoveSelectedOption: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });
                            }

                            if (this.props.onRequestRemoveSelectedOption) {
                                this.props.onRequestRemoveSelectedOption(event, data.selection);
                            }
                        },
                        onSelect: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });
                            }

                            if (this.props.onSelect) {
                                this.props.onSelect(event, data.selection);
                            }
                        },
                        onOpen: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    isOpened: true
                                });
                            }
                        },
                        onClose: (event, data) => {
                            if(this._isMounted) {
                                this.setState({
                                    isOpened: false
                                });
                            }
                        }
                    }}
                    labels={{
                        placeholder: 'Select SalesForce Field',
                    }}
                    options={this.filterOptionsHandler()}
                    selection={this.state.selection}
                    value={this.state.inputValue}
                    variant="readonly"
                    required={true}
                />
            </React.Fragment>
        );
    }
}

export default class GRAXRestoreMappingDefaults extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            _sfFieldOptions: null,
            _sfFieldOptionsList: null,
            _sfDefaultValues: null,
            _mappingOptions: [{id:"DEFAULT",label:"Default To"}, {id:"MASK",label:"Mask"}],
            _maskOptionsString: [
                {id: "STRING", label: "Mask the last 4 with XXXX"},
                {id: "STRINGRND", label: "Randomize"}
            ],
            _maskOptionsPhone:[
                {id: "PHONE", label: "Mask the last 4 with XXXX"},
                {id: "PHONERND", label: "Randomize"}
            ],
            _maskOptionsEmail:[
                {id: "EMAIL", label: "Mask the domain with @maskeddomain.com"},
                {id: "EMAILRND", label: "Randomize"}
            ],
            _maskOptionsDate:[
                {id: "DATE", label: "Mask the year with 2000"},
                {id: "DATERND", label: "Randomize"},
            ],
            _error: null
        };
    }

    isLoaded() {
        return this.state._error || (this.state._sfFieldOptions != null);
    }

    getDefaultValueStruct() {
        return {
            id: null,
            mapping: [this.state._mappingOptions[0]],
            masking: null,
            value: null,
            isOverride: false,
            selectedField: null
        };
    }

    getDefaultValuesList(_sfDefaultValues) {
        return _.map(_sfDefaultValues, (defaultValue, fieldName) => {
            var defaultValueStruct = this.getDefaultValueStruct();
            
            defaultValueStruct.id = fieldName;
            defaultValueStruct.mapping = _.filter( this.state._mappingOptions, (option,i)=>{
                return option.id === defaultValue.mapping;
            });
            if( !defaultValueStruct.mapping ){
                defaultValueStruct.mapping = [this.state._mappingOptions[0]];
            }
            let tempMsk = [];
            this.state._maskOptionsString.forEach(function(mskOpt){ tempMsk.push(mskOpt); });
            this.state._maskOptionsPhone.forEach(function(mskOpt){ tempMsk.push(mskOpt); });
            this.state._maskOptionsEmail.forEach(function(mskOpt){ tempMsk.push(mskOpt); });
            this.state._maskOptionsDate.forEach(function(mskOpt){ tempMsk.push(mskOpt); });
            
            defaultValueStruct.masking = _.filter( tempMsk, (option,i)=>{
                return option.id === defaultValue.masking;
            });
            defaultValueStruct.value = defaultValue.value;
            defaultValueStruct.isOverride = defaultValue.override;
            defaultValueStruct.selectedField = _.filter(this.state._sfFieldOptions || this.props.sfFieldOptions, (option, i) => {
                return option.id === fieldName;
            })[0] || null;

            return defaultValueStruct;
        });
    }

    componentDidMount() {
        /*var _sfFieldOptionsList = _.map(this.props.sfFieldOptions, function(item, i) {
            return item.id;
        });*/

        var _sfDefaultValues = this.props.sfDefaultValues || {};

        this.setState({
            _sfFieldOptions: this.props.sfFieldOptions,
            _sfDefaultValues: _sfDefaultValues,
            _sfDefaultValuesFields: Object.keys(_sfDefaultValues),
            _sfDefaultValuesList: this.getDefaultValuesList(_sfDefaultValues)
        });
    }

    /*shouldComponentUpdate(nextProps, nextState) {
        return (
            this.props.objectName !== nextProps.objectName
            || this.props.sfFieldOptions !== nextProps.sfFieldOptions
            || this.props.sfDefaultValues !== nextProps.sfDefaultValues
            || this.props.renderKey != nextProps.renderKey
        );
    }*/

    onDefaultValueChangeHandler(sfDefaultValue, selection) {
        var _sfDefaultValues = this.state._sfDefaultValues;

        if(selection) {
            var defaultValue = selection[0];
            
            sfDefaultValue.id = defaultValue.id;
            sfDefaultValue.selectedField = defaultValue;

            _sfDefaultValues[sfDefaultValue.id] = {
                mapping: sfDefaultValue.mapping?sfDefaultValue.mapping[0].id:"",
                masking: sfDefaultValue.masking?sfDefaultValue.masking[0].id:"",
                value: sfDefaultValue.value,
                override: sfDefaultValue.isOverride
            };
        } else {
            delete _sfDefaultValues[sfDefaultValue.id];
            sfDefaultValue.id = null;
            sfDefaultValue.selectedField = null;
        }

        this.setState({
            _sfDefaultValues: _sfDefaultValues,
            _sfDefaultValuesFields: Object.keys(_sfDefaultValues),
            _sfDefaultValuesList: this.state._sfDefaultValuesList
        });

        if(this.props.onDefaultValueChangeHandler) {
            this.props.onDefaultValueChangeHandler(sfDefaultValue.selectedField, _sfDefaultValues);
        }
    }

    addAnotherFieldHandler() {
        var _sfDefaultValuesList = this.state._sfDefaultValuesList;

        if(_sfDefaultValuesList.length === 0 || _sfDefaultValuesList[_sfDefaultValuesList.length - 1].id != null) {
            _sfDefaultValuesList.push(this.getDefaultValueStruct());

            this.setState({
                _sfDefaultValuesList: _sfDefaultValuesList
            });
        }
    }
    
    removeFieldHandler(i) {
        var _sfDefaultValues = this.state._sfDefaultValues;
        var _sfDefaultValuesList = this.state._sfDefaultValuesList;

        delete _sfDefaultValues[_sfDefaultValuesList[i].id];
        _sfDefaultValuesList.splice(i, 1);

        this.setState({
            _sfDefaultValues: _sfDefaultValues,
            _sfDefaultValuesFields: Object.keys(_sfDefaultValues),
            _sfDefaultValuesList: _sfDefaultValuesList
        });
    }

    render() {
        if (this.state._error) {
            return <div>Error: {this.state._error}</div>;
        } else if (!this.isLoaded()) {
            return (
                <div style={{ position: 'relative', height: '5rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            return (
                <React.Fragment>
                    {/*<div className="slds-scrollable_y" style={{maxHeight: '50vw'}}>*/}
                        <table className="slds-table slds-table_cell-buffer slds-table_bordered slds-no-row-hover">
                            <thead>
                                <tr className="slds-line-height_reset">
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={SALESFORCE_FIELD}>{SALESFORCE_FIELD}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={MAPPING}>
                                            {MAPPING}
                                            <div style={{texttransform: 'none', display: "inline"}} >
                                                <Tooltip
                                                    align="top left"
                                                    position="overflowBoundaryElement"
                                                    content='Default To - Set a value which will be used for this field on all records. Mask - Select one of the options from the "Value" picklist'
                                                >
                                                    <div style={{marginRight: '0.5rem', cursor: "pointer"}}>
                                                        <Icon
                                                            assistiveText={{ label: 'MAPPING' }}
                                                            category="utility"
                                                            name="info"
                                                            size="x-small"
                                                        />
                                                    </div>
                                                </Tooltip>
                                            </div>
                                        </div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={DEFAULT_VALUE}>{DEFAULT_VALUE}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={IS_OVERRIDE}>{IS_OVERRIDE}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate"></div>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {/* Iterate over fields */}
                                {_.map(this.state._sfDefaultValuesList, function(sfDefaultValue, i) {
                                    console.log(sfDefaultValue, i);

                                    var sfFieldName = sfDefaultValue.id;

                                    var selectedField = sfDefaultValue.selectedField;

                                    return (
                                        <tr className="slds-hint-parent" key={'sfFieldName: ' + (sfFieldName || '')}>
                                            <td data-label={SALESFORCE_FIELD} style={{width: '35vw'}}>
                                                {sfFieldName ||
                                                <GRAXRestoreMappingSFField
                                                    options={this.state._sfFieldOptions}
                                                    filterOptionsHandler={(options) => {
                                                        return _.filter(options, (option, i) => {
                                                            return this.state._sfDefaultValuesFields.indexOf(option.id) === -1;
                                                        });
                                                    }}
                                                    selection={[_.find(this.state._sfFieldOptions, (option) => {
                                                        return option.id === sfFieldName;
                                                    })]}
                                                    onSelect={(event, selection) => this.onDefaultValueChangeHandler(sfDefaultValue, selection)}
                                                    onRequestRemoveSelectedOption={(event, selection) => this.onDefaultValueChangeHandler(sfDefaultValue, selection)}
                                                />}
                                            </td>
                                            <td data-label={DEFAULT_VALUE} style={{width: '20vw'}}>
                                                {selectedField && selectedField.type !== "text" && selectedField.type !== "date" && (
                                                    this.state._mappingOptions[0].label
                                                )}
                                                {selectedField && (selectedField.type === "text" || selectedField.type === "date") && (
                                                    <GRAXRestoreMappingSFField
                                                        options={this.state._mappingOptions}

                                                        filterOptionsHandler={(options) => {
                                                            return this.state._mappingOptions;
                                                        }}
                                                        
                                                        selection={sfDefaultValue.mapping}

                                                        onSelect={(event, selection) => {
                                                            sfDefaultValue.mapping = selection;
                                                            sfDefaultValue.isOverride = false;

                                                            this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                        }}
                                                        onRequestRemoveSelectedOption={(event, selection) => {
                                                            sfDefaultValue.mapping = selection;
                                                            this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                        }}
                                                    />
                                                )}
                                            </td>
                                            <td data-label={DEFAULT_VALUE} style={{width: '35vw'}}>
                                                {selectedField && sfDefaultValue.mapping[0].id === "DEFAULT" && (
                                                    (
                                                        (selectedField.type === 'text' || selectedField.type === 'number') && (
                                                            <Input
                                                                defaultValue={sfDefaultValue.value}
                                                                assistiveText={{ label: selectedField.label }}
                                                                id={selectedField.id}
                                                                onBlur={(event) => {
                                                                    sfDefaultValue.value = event.target.value;
                
                                                                    this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                                }}
                                                            />
                                                        )
                                                    ) || (
                                                        selectedField.type === 'date' && (
                                                            <Datepicker
                                                                value={sfDefaultValue.value}
                                                                onChange={(event, data) => {
                                                                    sfDefaultValue.value = data.date;
                
                                                                    this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                                }}
                                                            />
                                                        )
                                                    ) || (
                                                        selectedField.type === 'boolean' && (
                                                            <Checkbox
                                                                defaultChecked={sfDefaultValue.value}
                                                                onChange={(isChecked) => {
                                                                    sfDefaultValue.value = isChecked;

                                                                    this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                                }}
                                                            />
                                                        )
                                                    )
                                                )}

                                                {selectedField && sfDefaultValue.mapping[0].id === "MASK" && (
                                                    ("sfType:" + selectedField  ) && 
                                                    <GRAXRestoreMappingSFField
                                                        /*options={this.state._maskOptions}*/
                                                        options={ 
                                                            selectedField.sfType === "phone"?this.state._maskOptionsPhone:
                                                            selectedField.sfType === "email"?this.state._maskOptionsEmail:
                                                            selectedField.type === "date"?this.state._maskOptionsDate:
                                                            this.state._maskOptionsString}
                                                        
                                                        filterOptionsHandler={(options) => {
                                                            return selectedField.sfType === "phone"?this.state._maskOptionsPhone:
                                                            selectedField.sfType === "email"?this.state._maskOptionsEmail:
                                                            selectedField.type === "date"?this.state._maskOptionsDate:
                                                            this.state._maskOptionsString;
                                                        }}
                                                        
                                                        selection={sfDefaultValue.masking}

                                                        onSelect={(event, selection) => {
                                                            sfDefaultValue.masking = selection;
                                                            this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                        }}
                                                        onRequestRemoveSelectedOption={(event, selection) => {
                                                            sfDefaultValue.masking = selection;
                                                            this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                        }}
                                                    />
                                                )}
                                            </td>
                                            <td data-label={IS_OVERRIDE} style={{width: '10vw'}}>
                                                {
                                                    sfDefaultValue.mapping[0] == null || sfDefaultValue.mapping[0].id !== "MASK" ? (
                                                        <Checkbox
                                                            key="checkbox_enabled"
                                                            defaultChecked={sfDefaultValue.isOverride}
                                                            onChange={(isChecked) => {
                                                                sfDefaultValue.isOverride = isChecked;

                                                                this.onDefaultValueChangeHandler(sfDefaultValue, [sfDefaultValue.selectedField]);
                                                            }}
                                                        />
                                                    ) : (
                                                        <Checkbox
                                                            key="checkbox_disabled"
                                                            value={false}
                                                            disabled={true}
                                                        />
                                                    )
                                                }
                                            </td>
                                            <td data-label={'Actions'} style={{width: '10vw'}}>
                                                <Button label="Remove Field" variant="base" onClick={() => {
                                                    this.removeFieldHandler(i);
                                                }}/>
                                            </td>
                                        </tr>
                                    );
                                }.bind(this))}
                            </tbody>
                        </table>
                    {/*</div>*/}

                    <div className="slds-p-top_medium">
                        <Button label="Add Field" variant="brand" onClick={() => {
                            this.addAnotherFieldHandler();
                        }}/>
                    </div>
                </React.Fragment>
            );
        }
    }
}